.sidebar{
    display: flex;
    flex-direction: column;
    /* 35 % of the fle this is to mitigate niche ka issue*/
    flex: 0.35;
}

.sidebar__search{
    display: flex;
    align-items: center;
    background-color:#f6f6f6;
    height: 30x;
    padding: 10px;
}

.sidebar__searchContainer{

    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar__searchContainer>input{

    border: none;
    margin-left: 10px;
}

.sidebar__searchContainer>.MuiSvgIcon-root{
    color: gray;
    

}
.sidebar_header{
    display:flex;
    padding: 20px;
    border-right: 1px solid lightgray;
    /* space around wont woek as app body is itself a chidl of app and is in flex 
    so it only takes up space of its contents*/
    justify-content: space-between;
}

.sidebar__headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* having responsive width */
    min-width: 10vw;
}

/* select all icon from header right parent div*/
.sidebar__headerRight >.MuiSvgIcon-root{

    margin-right: 2vw;
    /* !important is needed to overwrite matrial ui config*/
    font-size: 24px !important;
}

.sidebar_chats{
    flex:1;
    background-color:whitesmoke;
    overflow: scroll;
}

